import React from "react";
import style from "./Profile.module.css";
import capture from "./Capture.PNG";
import softwareImage from "./softwareImage.jpg";
import Typed from 'react-typed';

const Profile = () => {
  return (
    <React.Fragment>
      <div className={style.quote + " text-center  fixed-bottom"}>
        “If you think bigger , world becomes smaller”
      </div>
      <div
        style={{
          backgroundImage: `url(${softwareImage})`,
        }}
        className={
          style.my_bg +
          "  w-100 d-flex flex-column justify-content-center align-items-center"
        }
      >
        <div className={style.my_bg2 + " w-100 text-center m-0 p-0"}>
          <div className=" d-flex flex-column justify-content-center align-items-center">
          <div className={style.my_log}>VESIT05</div>
          </div>
          <div>
            <img className={style.img} src={capture} alt="pic1" />
          </div>

          <div className={style.my_styel}>Amar Chauhan
        
          
          </div>
          <div className={style.my_styel + " " + style.stream + " sticky-top"}>
            Master Of Computer Application
          </div>
          <div className={style.my_styel + " " + style.stream + " "}>
              
            <Typed
                strings={[
                    '  Software Engineer',
                    ]}
                    typeSpeed={70}
                    backSpeed={50}
                    
                    loop >
                   
                </Typed>
            
          </div>
          <div className={style.icon}>
            <i className="fas fa-arrow-alt-circle-down "></i>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Profile;
